exports.push([module.id, "\n.page-confirmation.loading:after {\n  height: 4em;\n  width:  4em;\n  left:   calc(50% - (4em / 2));\n  top:    calc(50% - (4em / 2));\n}\n.page-confirmation {\n  min-height: 85vh;\n}\n", "",{"version":3,"sources":["/Users/voz/Workspaces/marketplace-v2/app/javascript/components/pages/app/javascript/components/pages/Confirmation.vue"],"names":[],"mappings":";AAmDA;EACA,WAAA;EACA,WAAA;EACA,6BAAA;EACA,6BAAA;AACA;AAEA;EACA,gBAAA;AACA","file":"Confirmation.vue?vue&type=style&index=0&lang=css&","sourcesContent":["<template lang=\"pug\">\n  .page-confirmation.loading\n\n</template>\n\n<script>\n  import { mapActions, mapGetters } from \"vuex\";\n\n\n  export default {\n    name: 'Confirmation',\n    components: {},\n    data() {\n      return {\n        loading: false,\n      }\n    },\n    computed: mapGetters(['user']),\n    created() {\n      const {confirmation_token} = this.$route.query;\n\n      this.$store.dispatch(\"confirmUser\", confirmation_token).then(res => {\n        const {user, token} = res.body\n        if (user) {\n          const {attributes} = user.data\n          this.$auth.user(attributes)\n          this.$store.dispatch(\"setUser\", attributes);\n          this.$auth.token(null, token)\n          this.$auth.watch.authenticated = true\n          this.$auth.watch.loaded = true\n          this.$cookie.set('rememberMe', true, 365)\n          setTimeout(() => {\n            this.$toast.success(\"Thanks for confirming your email\");\n          }, 400)\n          this.$router.push({name: 'Services'})\n        } else {\n          if (this.user && this.user.id) {\n            this.$router.push({name: 'Services'})\n          } else {\n            this.$toast.error(\"You already confirmed your email. Please log in\");\n            this.$router.push({name: 'SignIn'})\n          }\n        }\n      })\n    },\n\n\n  }\n</script>\n\n<style>\n  .page-confirmation.loading:after {\n    height: 4em;\n    width:  4em;\n    left:   calc(50% - (4em / 2));\n    top:    calc(50% - (4em / 2));\n  }\n\n  .page-confirmation {\n    min-height: 85vh;\n  }\n</style>\n\n\n"]}]);

